* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  color: rgb(63, 63, 63);
  font-family: "Poppins", sans-serif;
  height: auto;
}
.Main_Cntnr {
  background-color: white;
  height: 100vh;
  width: 100%;
  display: flex;
  position: relative;
}
.logobox_Access {
  width: 17%;
  height: 100vh;
  position: fixed;
  display: flex;
  align-items: center;
  background-color: whitesmoke;
  flex-direction: column;
  box-shadow: 0 0 4px;
}
.logo {
  margin: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70px;
  background-color: #fff;
}
.Info_Accs {
  display: flex;
  justify-content: flex-start;
  margin: 20% 0 0 5%;
  flex-direction: column;
}
.Acc_Info,
.Comp_Info,
.Acc_lvl {
  padding: 6px 35px 6px 6px;
  margin: 4px 0;
}
.Acc_Info:hover,
.Comp_Info:hover,
.Acc_lvl:hover {
  border-radius: 20px;
  border: 1px solid rgba(128, 128, 128, 0.753);
  background-color: #fff;
}
.Acc_Info a,
.Comp_Info a,
.Acc_lvl a {
  text-decoration: none;
  color: #334d6e;
}

.leftbtns_Access {
  margin-top: 100%;
}
.Emailbtn_Access {
  width: 140px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: #008bdf;
  color: #fff;
  border: none;
  margin: 13px auto;
  padding: 8px;
  font-weight: bold;
  font-size: 16px;
  border-radius: 4px;
}

.Supportbtn_Access {
  width: 140px;
  cursor: pointer;
  background-color: #c4dceb;
  color: #192a3e;
  border: none;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 13px auto;
  padding: 8px;
  font-weight: bold;
  font-size: 16px;
  border-radius: 4px;
}
.lftmail_Access .fa-envelope,
.lftphone_Access .fa-phone-alt {
  padding-right: 8px;
  color: #192a3e;
}
.lftmail_Access a {
  color: #192a3e;
  text-decoration: none;
}
.lftmail_Access,
.lftphone_Access {
  display: flex;
  align-items: center;
  justify-content: center;
}
.lftphone_Access {
  color: #192a3e;
  text-align: center;
  cursor: pointer;
}

.NavContainerAccess {
  width: 83%;
  height: auto;
  position: absolute;
  left: 17%;
}
.navbar_Access {
  height: 50px;
  width: 82.5%;
  position: fixed;
  left: 17.1%;
  z-index: 1;
  display: flex;
  box-shadow: 0 1px 4px grey;
  background-color: whitesmoke;
  align-items: center;
  justify-content: flex-end;
}
.list {
  display: flex;
}

.list li {
  margin: 0 30px 0 10px;
  list-style: none;
}
.list li a {
  text-decoration: none;
  color: #192a3e;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.list li a .fas,
.list li a .far {
  padding-right: 5px;
}

.whole {
  width: 100%;
  position: absolute;
  top: 52px;
}
.My_Js_My_As {
  display: flex;
  align-items: center;
  font-size: 1.2em;
  padding-bottom: 2px;
  border-bottom: 1px solid rgba(128, 128, 128, 0.527);
}
.my_job {
  background-color: rgba(128, 128, 128, 0.527);
  color: grey;
  padding-left: 5px;
  display: flex;
  height: 70px;
  align-items: center;
}
.my_job h1,
.my_acc h1 {
  font-size: 1.5em;
  font-weight: 100;
  cursor: pointer;
}
.arrow {
  width: 40px;
  height: 55px;
  font-weight: 100;
  line-height: 70px;
  background-color: #fff;
  border-left: 40px solid rgba(128, 128, 128, 0.527);
  border-top: 35px solid transparent;
  border-bottom: 35px solid transparent;
}
.my_acc {
  margin-left: 20px;
  color: grey;
}
.Acc_h2 h2 {
  font-size: 1.7em;
  line-height: 40px;
  font-weight: 100;
  color: grey;
  margin: 5px 0 20px 20px;
}
.pres_para {
  margin: 15px 0 10px 15px;
}
.abt_list {
  margin: 10px 0 10px 35px;
  color: red;
}
.table {
  margin: 20px 20px 10px 20px;
  width: 70%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.table > div p {
  padding: 5px 0;
}
.table > div .fa-question-circle {
  color: rgb(22, 217, 243);
  font-size: 20px;
}
.btns_div {
  display: flex;
  align-items: center;
  width: 70%;
  justify-content: space-between;
  margin: 20px;
}
.Add_btn button,
.Save_btn button {
  background-color: #76cf21;
  padding: 8px 6px;
  color: white;
  font-weight: 100;
  font-size: 14px;
  border-radius: 6px;
  cursor: pointer;
  border: none;
  outline: none;
}
.Add_btn button:hover,
.Save_btn button:hover {
  background-color: #fff;
  color: #76cf21;
  border: 1px solid #76cf21;
}
@media all and (min-width: 320px) and (max-width: 480px) {
