    padding: 2px 3px;
    font-weight: normal;
    font-size: 5.5px;

    border-radius: 2px;
  }
  .lftmail_Access .fa-envelope,
  .lftphone_Access .fa-phone-alt {
    padding-right: 1px;
  }
  .lftmail_Access {
    display: block;
    text-align: center;
    font-size: 5px;
