    padding-left: 3px;

    height: 24px;
  }
  .my_job h1,
  .my_acc h1 {
    font-size: 1.4em;
    padding: 0 5px;
  }
  .arrow {
    width: 10px;
    height: 20px;

    border-left: 15px solid rgba(128, 128, 128, 0.527);
    border-top: 12px solid transparent;
    border-bottom: 12px solid transparent;
  }
  .my_acc {
    margin-left: 6px;
  }
  .Acc_h2 h2 {
    font-size: 1.4em;
    line-height: 25px;
    margin: 1px 0 4px 10px;
  }
  .pres_para {
    margin: 1px 0 5px 15px;
  }
  .abt_list {
    margin: 7px 0 6px 25px;
    font-size: 6px;
  }
  .table {
    margin: 10px 10px 6px 15px;
    width: 80%;
    font-size: 6.5px;
  }
  .table > div p {
    padding: 3px 0;
  }
  .table > div .fa-question-circle {
    font-size: 7px;
  }
  .btns_div {
    width: 90%;

    margin: 6px;
  }
  .Add_btn button,
  .Save_btn button {
    padding: 3px 4px;

    font-size: 6.5px;
    border-radius: 3px;
  }
}

